.Nav {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	max-width: 400px;
	background-color: rgba(255, 255, 255, 0.7);
	padding: 16px;
	border-top-left-radius: 8px;
	border-top-right-radius: 8px;
}

.Nav-logo {
	font-size: 2rem;
	font-weight: bold;
	margin: 0;
	color: #293684;
}

.Welcome {
	display: flex;
	flex-direction: column;
}

.Welcome-cancel {
	margin-left: auto;
}

.Search {
	display: flex;
	flex-direction: column;
}

.Search-select {
	font-size: 1rem;
	margin: 16px 0;
	border-radius: 8px;
	padding: 8px;
	border: none;
}

.Search-continue {
	margin-left: auto;
}

.Passengers {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 0 8px;
}

.Passengers input {
	font-size: 1rem;
	margin: 16px 0;
	border-radius: 8px;
	padding: 8px;
	border: none;
	width: 100%;
	box-sizing: border-box;
}

.Passengers-buttons {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 16px;
	gap: 16px;
	width: 100%;
	margin-top: 8px;
}

.Passengers-title {
	margin-bottom: 16px;
}

.Tickets {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.Tickets-finalizar {
	margin-left: auto;
}

.Tickets-ticket {
	display: grid;
	grid-template-columns: 1fr 5fr;
	margin-bottom: 16px;
}

.Tickets-country {
	background-color: white;
	padding: 16px;
	border-top-right-radius: 8px;
	border-bottom-right-radius: 8px;
	border-top-left-radius: 4px;
	border-bottom-left-radius: 4px;
	font-weight: bold;
	text-transform: uppercase;
	font-size: 1.2rem;
	display: flex;
	justify-content: center;
	align-items: center;
	border-right: 1px dashed #4f54a7;
}

.Tickets-passengers {
	background-color: rgba(151, 149, 240, 0.5);
	padding: 16px;
	border-top-right-radius: 4px;
	border-bottom-right-radius: 4px;
	border-top-left-radius: 8px;
	border-bottom-left-radius: 8px;
	font-weight: bold;
	display: flex;
	justify-content: center;
	flex-direction: column;
	position: relative;
	border-left: 1px dashed #4f54a7;
}

.Tickets-passengers p {
	margin: 2px;
}

.Tickets-passengers span {
	position: absolute;
	top: -10px;
	right: 10px;
	font-size: 60px;
	color: #4f54a7;
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
}

.StepsLayout {
	background-color: rgba(255, 255, 255, 0.5);
	width: 100%;
	max-width: 400px;
	padding: 16px;
	border-bottom-left-radius: 8px;
	border-bottom-right-radius: 8px;
}

.BaseLayout {
	background-color: #fed6e3;
	background-image: url(/react-state-machine/static/media/background-travel.228bdd7c.jpg);
	background-size: cover;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 50px;
	box-sizing: border-box;
}

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

body {
	margin: 0;
	font-family: "Roboto", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.button {
	border: none;
	background-color: #4f54a7;
	padding: 8px 16px;
	border-radius: 8px;
	color: white;
	font-weight: bold;
	cursor: pointer;
	transition: all ease 300ms;
}

.button:hover {
	background-color: #293684;
}

.button-secondary {
	border: none;
	background-color: rgba(255, 255, 255, 0.8);
	padding: 8px 16px;
	border-radius: 8px;
	color: #4f54a7;
	font-weight: bold;
	cursor: pointer;
	transition: all ease 300ms;
}

.button-secondary:hover {
	color: #293684;
	background-color: white;
}

.title {
	font-size: 1.3rem;
	margin: 0;
}

.text {
	font-size: 1rem;
	margin: 4px 0;
	width: 100%;
	text-align: left;
}

